.header {
  &__inner {
    display: flex;
    align-items: center;
    justify-content: space-between;
    height: 95px;
  }
}
.menu {
  display: flex;
  &__link {
    a {
      color: #868686;
      font-family: "Lato";
      font-weight: 400;
      font-size: 14px;
      text-transform: uppercase;
      transition: all 0.3s;
      padding: 12px 20px;
    }
    a:hover {
      color: #ffffff;
      background-color: #ffbb42;
      border-radius: 4px;
    }
  }
}

//-----------------------------
.wrapper {
  background-color: #709dca;
  height: 589px;
}
.intro {
  height: 710px;
  &__inner {
    padding-top: 100px;
    text-align: center;
  }

  &__title {
    color: #ffffff;
    font-family: "Gotham";
    font-size: 35px;
    text-transform: uppercase;
  }

  &__text {
    color: #ffffff;
    font-size: 16px;
    font-weight: 300;
    margin-bottom: 33px;
  }

  &__form {
    margin-bottom: 85px;
    input:nth-child(1) {
      background-image: url("../img/user.png");
      background-repeat: no-repeat;
      background-position: 19px;
    }
    input:nth-child(2) {
      background-image: url("../img/mail.png");
      background-repeat: no-repeat;
      background-position: 19px;
    }
    input:nth-child(3) {
      background-image: url("../img/phone.png");
      background-repeat: no-repeat;
      background-position: 19px;
    }
    button {
      color: #ffffff;
      font-size: 14px;
      font-weight: 400;
      background-color: #ffbb42;
      border: none;
      border-radius: 4px;
      width: 211px;
      height: 46px;
      cursor: pointer;
    }
  }
  &__input {
    padding: 18px 41px;
    border-radius: 4px;
    border: none;
    margin-right: 10px;
    &::placeholder {
      color: #a9a9a9;
      font-size: 13px;
      font-weight: 400;
    }
  }
}

