// Слайдер

.services {
  padding-top: 90px;
  padding-bottom: 65px;
  &__title {
    text-align: center;
    font-size: 30px;
    font-family: "Gotham";
    text-transform: uppercase;
    margin: 0;
  }

  &__text {
    color: #394b50;
    font-size: 16px;
    font-weight: 400;
    text-align: center;
    margin-bottom: 90px;
  }

  &__inner {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }

  &__item {
    display: flex;
    max-width: 50%;
    &:nth-child(even) {
      padding-left: 95px;
    }
    img {
      margin-top: 16px;
      align-self: flex-start;
    }
  }

  &__item-info {
    padding-left: 39px;
  }

  &__item-title {
    color: #394b50;
    font-family: "Lato";
    font-size: 16px;
    font-weight: 700;
  }

  &__item-text {
    color: #7b7b7b;
    font-size: 13px;
    font-weight: 400;
    line-height: 22px;
    margin-bottom: 60px;
  }
}

.about {
  overflow: hidden;
  background-color: #ffc155;
  margin-bottom: 100px;

  &__inner {
    display: flex;
    flex-wrap: wrap;
  }

  &__foto {
    width: 50%;
    height: 500px;
    position: relative;
  }
  &__img {
    position: absolute;
    top: 0;
    right: 0;
    z-index: 1;
  }

  &__content {
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    padding-left: 80px;
  }

  &__title {
    color: #ffffff;
    font-family: "Gotham";
    font-size: 30px;
    font-weight: 400;
    margin-bottom: 25px;
  }

  &__text {
    color: #ffffff;
    font-size: 13px;
    line-height: 24px;
    margin-bottom: 30px;
  }

  &__btn {
    color: #ffffff;
    font-size: 14px;
    font-weight: 400;
    border: 1px solid #e9af49;
    width: 145px;
    height: 46px;
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
  }
}
