@charset "UTF-8";
@import url(./vendor/normalize.css);
@import url(../../node_modules/swiper/swiper-bundle.css);
:root {
  --color-black: #000;
  --color-white: #fff;
}

@font-face {
  font-family: "Gotham";
  src: url("../fonts/Gotham-Black.woff2") format("woff2");
  font-weight: 900;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Lato";
  src: url("../fonts/Lato-Bold.woff2") format("woff2");
  font-weight: 700;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Lato";
  src: url("../fonts/Lato-Light.woff2") format("woff2");
  font-weight: 300;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "Lato";
  src: url("../fonts/Lato-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "OpenSans";
  src: url("../fonts/OpenSans-Light.woff2") format("woff2");
  font-weight: 300;
  font-display: swap;
  font-style: normal;
}

@font-face {
  font-family: "OpenSans";
  src: url("../fonts/OpenSans-Regular.woff2") format("woff2");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}

html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  font-family: "OpenSans", sans-serif;
}

a {
  text-decoration: none;
  color: inherit;
}

ul {
  list-style: none;
  padding: 0;
  margin: 0;
}

.is-outer {
  position: relative !important;
  left: -9999px !important;
}

.site-container {
  overflow: hidden;
}

.stop-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.is-hidden {
  display: none !important;
}

.for-mobile {
  display: none;
}

@media (max-width: 1024px) {
  .for-mobile {
    display: block;
  }
}

.error {
  border: 2px #ff4019 solid !important;
}

.visually-hidden {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px);
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

.btn-reset {
  border: none;
  background: transparent;
  cursor: pointer;
}

.container {
  margin: 0 auto;
  max-width: 1170px;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

input {
  outline: none;
}

.header__inner {
  display: flex;
  align-items: center;
  justify-content: space-between;
  height: 95px;
}

.menu {
  display: flex;
}

.menu__link a {
  color: #868686;
  font-family: "Lato";
  font-weight: 400;
  font-size: 14px;
  text-transform: uppercase;
  transition: all 0.3s;
  padding: 12px 20px;
}

.menu__link a:hover {
  color: #ffffff;
  background-color: #ffbb42;
  border-radius: 4px;
}

.wrapper {
  background-color: #709dca;
  height: 589px;
}

.intro {
  height: 710px;
}

.intro__inner {
  padding-top: 100px;
  text-align: center;
}

.intro__title {
  color: #ffffff;
  font-family: "Gotham";
  font-size: 35px;
  text-transform: uppercase;
}

.intro__text {
  color: #ffffff;
  font-size: 16px;
  font-weight: 300;
  margin-bottom: 33px;
}

.intro__form {
  margin-bottom: 85px;
}

.intro__form input:nth-child(1) {
  background-image: url("../img/user.png");
  background-repeat: no-repeat;
  background-position: 19px;
}

.intro__form input:nth-child(2) {
  background-image: url("../img/mail.png");
  background-repeat: no-repeat;
  background-position: 19px;
}

.intro__form input:nth-child(3) {
  background-image: url("../img/phone.png");
  background-repeat: no-repeat;
  background-position: 19px;
}

.intro__form button {
  color: #ffffff;
  font-size: 14px;
  font-weight: 400;
  background-color: #ffbb42;
  border: none;
  border-radius: 4px;
  width: 211px;
  height: 46px;
  cursor: pointer;
}

.intro__input {
  padding: 18px 41px;
  border-radius: 4px;
  border: none;
  margin-right: 10px;
}

.intro__input::placeholder {
  color: #a9a9a9;
  font-size: 13px;
  font-weight: 400;
}

.wrapper-slider {
  width: 445px;
  text-align: center;
  margin: 0 auto;
  position: relative;
}

.wrapper-slider::before {
  content: "";
  position: absolute;
  background-image: url("../img/slider.png");
  width: 648px;
  height: 344px;
  top: -19px;
  left: -102px;
}

