.services {
  padding-top: 90px;
  padding-bottom: 65px;
}

.services__title {
  text-align: center;
  font-size: 30px;
  font-family: "Gotham";
  text-transform: uppercase;
  margin: 0;
}

.services__text {
  color: #394b50;
  font-size: 16px;
  font-weight: 400;
  text-align: center;
  margin-bottom: 90px;
}

.services__inner {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}

.services__item {
  display: flex;
  max-width: 50%;
}

.services__item:nth-child(even) {
  padding-left: 95px;
}

.services__item img {
  margin-top: 16px;
  align-self: flex-start;
}

.services__item-info {
  padding-left: 39px;
}

.services__item-title {
  color: #394b50;
  font-family: "Lato";
  font-size: 16px;
  font-weight: 700;
}

.services__item-text {
  color: #7b7b7b;
  font-size: 13px;
  font-weight: 400;
  line-height: 22px;
  margin-bottom: 60px;
}

.about {
  overflow: hidden;
  background-color: #ffc155;
  margin-bottom: 100px;
}

.about__inner {
  display: flex;
  flex-wrap: wrap;
}

.about__foto {
  width: 50%;
  height: 500px;
  position: relative;
}

.about__img {
  position: absolute;
  top: 0;
  right: 0;
  z-index: 1;
}

.about__content {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  padding-left: 80px;
}

.about__title {
  color: #ffffff;
  font-family: "Gotham";
  font-size: 30px;
  font-weight: 400;
  margin-bottom: 25px;
}

.about__text {
  color: #ffffff;
  font-size: 13px;
  line-height: 24px;
  margin-bottom: 30px;
}

.about__btn {
  color: #ffffff;
  font-size: 14px;
  font-weight: 400;
  border: 1px solid #e9af49;
  width: 145px;
  height: 46px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}
